
export class CookieServiceMock {
/**
 * @param {?} key
 * @return {?}
 */
getItem(key: string): string | null {
        return this[key] && this[key].data || null;
    }
/**
 * @param {?} key
 * @param {?} data
 * @param {?} expiration
 * @param {?} path
 * @return {?}
 */
setItem(key: string, data: string, expiration: Date | null, path: string | null): void {
        this[key] = {data, expiration, path};
    }
}
